body {
    background-color: peachpuff;
}

.boxes-container{
    background-color: white;
    border: 2px solid tomato;
    height: 600px;
    width: 600px;
    margin: auto;
    margin-top: 10vh;
    position: relative;
}

.box{
    height: 200px;
    width: 200px;
    color: white;
    font-size: 30px;
    font-family: 'Courier New', Courier, monospace;
}

.box-1{
    background-color: black; 
    position: absolute;
    top: 0px;
    right: 0px;
    
}

.box-2{
    background-color: grey; 
    position: absolute;
    top: 300px;
    left: 200px;
    transform: scaleX(3) scaleY(2);
    
}

.box-3{
    background-color: brown;
    position: absolute;
    top: 0px;
    left: 0px;
    
}